@import url('https://fonts.googleapis.com/css2?family=Anton&family=Bodoni+Moda+SC:ital,opsz,wght@0,6..96,400..900;1,6..96,400..900&family=Krona+One&family=Playwrite+CZ:wght@100..400&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

* {
    margin: 0; padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none; color: #333;
    font-family: 'Roboto', sans-serif;
} html {
    scroll-behavior: smooth;
} ::-webkit-scrollbar { 
    display: none;
} .titleHead {
    font-family: 'Roboto', sans-serif;
    font-size: 75px; font-weight: bold;
} .title {
    font-family: 'Roboto', sans-serif;
    font-size: 75px; font-weight: bold;
} .fw {
    color: white;
}

@media screen and (max-width: 576px) {
    .title {
        font-size: 40px;
    } .text {
        font-size: 15px;
    }
}